import ModelBase from "../../../framework/model/ModelBase";

export default class ShareModel extends ModelBase{
    private _ShareMoney:number = 0

    public set ShareMoney(num:number)
    {
        this._ShareMoney = num
    }

    public get ShareMoney()
    {
        return this._ShareMoney
    }

    public get Name()
    {
        return "ShareModel";
    }

    public initData(data)
    {
        this.ShareMoney = data
    }
}